:root {
    --white: #ffffff;
    --ice-white: #f7f7f7;
    --red: #e31b23;
    --secondary-red: #dc1a22;
    --gray: #00000029;
    --secondary-gray: #828282;
    --dark-gray: #333333;
    --gray-text: #545454;
    --green: #2bb20b;
    --secundary-green: #309e16;
    --placeholder: #a9aaab;
    --black: #000000;
    --blue: #1987e8;
    --darkpurple: #343175;
    --orange: #f08d06;
}

* {
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    padding: 0;
    outline: none;
}

body {
    margin: 1em 0;
    background: var(--ice-white);
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
}

/* Global btn */
.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--green);
    color: var(--green);
    background: transparent;
    padding: 0.5em 0.7em;
    width: 100%;
    cursor: pointer;
    font-weight: 600;
    text-transform: uppercase;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}
.btn svg {
    margin-right: 0.8em;
}
.btn:hover,
.btn:focus {
    background: var(--green);
    color: var(--white);
}
.btn:hover svg,
.btn:focus svg {
    fill: var(--white);
}

.container-promotions {
    color: var(--dark-gray);
    display: flex;
    flex-flow: row wrap;
    gap: 3.4375em;
    justify-content: flex-start;
}

/* Grid layout */
.grid {
    width: 100%;
    max-width: 70em;
    margin: 0 auto;
}

.flickity-page-dots {
    bottom: 1em;
}
.title {
    font-weight: 400;
    font-size: 1.25em;
    color: var(--blue);
    margin-top: -1em;
}
.subTitle {
    font-size: 1.125em;
    color: var(--dark-gray);
    margin: 2.5em 0 0.6em;
    font-weight: 600;
}

.flickity-button {
    background: none !important;
    color: var(--ice-white) !important;
    border: none !important;
    width: 35px;
}
.flickity-page-dots {
    .dot {
        background: var(--white) !important;
    }
}
